  }
}

.heading-div {
  padding: 93px 0px;

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 48px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
  }

  p {
    color: #fff;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }
}

.future-vision {
  margin: 60px 0px 120px 0px;

  h4 {
    color: #fff;
    font-family: Urbanist;
    font-size: 16px;
    font-style: normal;
    font-weight: 200;
    line-height: 22.393px;
    /* 139.957% */
    text-transform: uppercase;
  }

  h1 {
    color: #fff;
    font-family: Urbanist;
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 60px;
    /* 150% */
    letter-spacing: -0.1px;
    margin: 0px;
  }

  .vision-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 43px;
    margin-top: 50px;

  }


  .vision {
    position: relative;
    /* needed for ::after positioning */
    padding: 102px 16px 75px;
    height: max-content;
    border-radius: 37px;
    border: 1.5px solid var(--1, #6582ff);
    background: var(
      --1,
      linear-gradient(
        0deg,
        rgba(101, 130, 255, 0.18) 0%,
        rgba(192, 91, 218, 0.18) 100%
      )
    );
    overflow: hidden;

    /* hides ::after overflow */
    h1 {
      color: #fff;
      font-family: Urbanist;
      font-size: 26px;
      font-style: normal;
      font-weight: 600;
      line-height: 60px;
      letter-spacing: -0.1px;
      margin-top: 18px;
    }

    p {
      color: #a6a6a6;
      font-family: Urbanist;
      font-size: 18px;
      font-style: normal;
      font-weight: 400;
      line-height: 141.048%;
    }
  }

  .vision:nth-of-type(2) {
    margin-top: 90px;
    /* adjust as needed */
  }

  .vision::after {
    content: "";
    position: absolute;
    width: 271px;
    height: 271px;
    background: url("../../../../assets/images/vision-rings.png");
    top: 0px;
    right: 0px;
    background-position: right top;
    background-repeat: no-repeat;
    background-size: contain;
  }

  .white-circle {
    background-color: #fff;
    padding: 13px;
    border-radius: 999px;
    width: min-content;
  }
}

.joincomunity-div {
  border-radius: 24px;
  background: var(
    --new,
    linear-gradient(130deg, #492fb2 -1.74%, #adadad 108.02%)
  );
  padding: 47px;
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    color: #fff;
    text-align: center;
    font-family: Urbanist;
    font-size: 40px;
    font-style: normal;
    font-weight: 600;
    line-height: 60px;
    /* 150% */
    letter-spacing: -0.1px;
  }

  p {
    color: #fff;
    text-align: center;
    font-family: Urbanist;
    font-size: 18px;
    font-style: normal;
    font-weight: 500;
    line-height: 32px;
    /* 177.778% */
    letter-spacing: -0.1px;
    width: 60%;
  }
}
.impact-story {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 32px;
  margin-bottom: 120px;
}
.story:hover {
  .icon-circle {
    transform: scale(1.07);
  }
  .story-icon {
    position: absolute;
    top: -20px;
    right: -20px;
  }
}
.story {
  border-radius: 15.41px;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px;
  .icon-circle {
    background-color: rgba(99, 80, 176, 0.09);
    width: 69.346px;
    height: 69.346px;
    position: relative;
    border-radius: 9999px;
    transition: all 0.3s ease-in-out;
  }
  .story-icon {
    position: absolute;
    top: -15px;
    right: -15px;
    transition: all 0.3s ease-in-out;
  }
  h1 {
    color: #272725;
    text-align: center;
    font-family: Urbanist;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-top: 35px;
  }
}

  @media (max-width: 1000px){
    .vision-cards{
      grid-template-columns: 1fr !important;
    }
      .vision:nth-of-type(2) {
    margin-top: 0px !important;
    /* adjust as needed */
  }
  .joincomunity-div{
    p{
      width: 100% !important;
    }
  }
  }
